import * as React from 'react';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import Menu from '@mui/material/Menu';
import MenuIcon from '@mui/icons-material/Menu';
import Container from '@mui/material/Container';
import Button from '@mui/material/Button';
import MenuItem from '@mui/material/MenuItem';
import {Link} from 'react-router-dom';

const pages = ['Blog', 'Gallery', 'Users'];

const ResponsiveAppBar = () => {
  const [anchorElNav, setAnchorElNav] = React.useState<null | HTMLElement>(null);

  const handleOpenNavMenu = (event: React.MouseEvent<HTMLElement>) => {
    setAnchorElNav(event.currentTarget);
  };

  const handleCloseNavMenu = () => {
    setAnchorElNav(null);
  };

  const mapPagesFomMenu = pages.map((page) =>
    (<Link
        to={page.toLocaleLowerCase()}
        key={page}
        style={{textDecoration: 'none'}}>
        <MenuItem
          key={page}
          onClick={handleCloseNavMenu}>
          <Typography
            textAlign='center'>
            {page}
          </Typography>
        </MenuItem>
      </Link>
    ))

  const mapPagesForSmallMenu = pages.map((page) =>
    (<Link
        to={page.toLocaleLowerCase()}
        key={page}
        style={{textDecoration: 'none'}}>
        <Button
          onClick={handleCloseNavMenu}
          sx={{my: 2, color: 'white', display: 'block'}}>
          {page}
        </Button>
      </Link>
    ))

  return (
    <AppBar
      sx={{backgroundColor: 'dimgrey'}}
      position='static'>
      <Container
        maxWidth='xl'>
        <Toolbar
          disableGutters>
          <Box
            sx={{
              flexGrow: 1,
              display: {xs: 'flex', md: 'none', justifyContent: 'flex-end'}
            }}>
            <IconButton
              size='large'
              aria-label='app menu'
              aria-controls='menu-appbar'
              aria-haspopup='true'
              onClick={handleOpenNavMenu}
              color='inherit'
            >
              <MenuIcon/>
            </IconButton>
            <Menu
              id='menu-appbar'
              anchorEl={anchorElNav}
              anchorOrigin={{
                vertical: 'bottom',
                horizontal: 'left',
              }}
              keepMounted
              transformOrigin={{
                vertical: 'top',
                horizontal: 'left',
              }}
              open={Boolean(anchorElNav)}
              onClose={handleCloseNavMenu}
              sx={{
                display: {xs: 'block', md: 'none'},
              }}>
              {mapPagesFomMenu}
            </Menu>
          </Box>
          <Box
            sx={{
              flexGrow: 1,
              display: {xs: 'none', md: 'flex', justifyContent: 'flex-end'}
            }}>
            {mapPagesForSmallMenu}
          </Box>
        </Toolbar>
      </Container>
    </AppBar>
  );
};
export default ResponsiveAppBar;